.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 150vh;
    z-index: 1000;
    background: #46564c84;
    /* background: #583a88bd ; */
    /* opacity: 80%; */
  }
  
  .modal {
    position: fixed;
    top: 10vh;
    left: 10%;
    width: 80%;
    z-index: 10000;
    overflow: hidden;
    opacity: none;
    transition: opacity 0.25s ease transform 0.25s;
    color: white;
    background-color: #343a3f;
  }
  
  .content {
    padding: 1rem;
    color: white;
  }
  
  .actions {
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
  }
  
  @media (min-width: 768px) {
    .modal {
      left: calc(50% - 20rem);
      width: 40rem;
    }
  }
  